<template>
	<view class="news">
		<news-item @itemClick="goDetail" :list="news"></news-item>
	</view>
</template>

<script>
	import newsItem from "../../components/news_item/news-item.vue"
	export default {
		data() {
			return {
				news:[]
			}
		},
		components:{"news-item":newsItem},
		methods: {
			goDetail(id){
				 uni.navigateTo({
					url:"/pages/news-detail/news-detail?id="+id
				})
			},
			async getNews(){
				const res=await this.$myRequest({
					url:"/news.php?page=1",
					method:"POST"
				})
				this.news=res.data
			}
		},
		onLoad(){
			this.getNews()
		}
	}
</script>

<style>
.news .news_item{
		display: flex;
		padding: 10rpx 20rpx;
		border-bottom: 1px solid #b50e03;
	}
	.news .news_item image{
		min-width: 200rpx;
		max-width: 200rpx;
		height: 150rpx;
	}
	.news .news_item .right{
		margin-left: 15rpx;
		display: flex;
		flex-direction: column;
		justify-content: space-between;
	}
	.news .news_item .tit{
		font-size: 30rpx;
	}
	.news .news_item .info{
		font-size: 24rpx;
	}
	.news .news_item .info text:nth-child(2){
		margin-left: 30rpx;
	} 
</style>
